.passage-page {
  display: flex;
  height: 100vh; /* Full viewport height */
}

.text-passage-container {
  flex: 1;
  overflow-y: auto;
  resize: horizontal;
  min-width: 200px; /* Minimum width for the resizable element */
  max-width: 50%; /* Maximum width for the resizable element */
  border-right: 2px solid #eeeeee; /* Divider */
  padding: 1em;
}

.questions-list-container {
  flex: 1;
  resize: horizontal;
  overflow-y: auto;
  padding: 2em;
}